.checkout-container {
    display: flex;
    flex-direction: column;
}

.checkout-container .error {
    width: 100%;
    margin-top: 0.25rem;
    font-size: .875em;
    color: #dc3545;
}

.checkout-container .errorInputField {
    border-color: #dc3545;
    padding-right: calc(1.5em + 0.75rem);
    background-image: url("data:image/svg+xml,%3csvg xmlns=%27http://www.w3.org/2000/svg%27 viewBox=%270 0 12 12%27 width=%2712%27 height=%2712%27 fill=%27none%27 stroke=%27%23dc3545%27%3e%3ccircle cx=%276%27 cy=%276%27 r=%274.5%27/%3e%3cpath stroke-linejoin=%27round%27 d=%27M5.8 3.6h.4L6 6.5z%27/%3e%3ccircle cx=%276%27 cy=%278.2%27 r=%27.6%27 fill=%27%23dc3545%27 stroke=%27none%27/%3e%3c/svg%3e");
    background-repeat: no-repeat;
    background-position: right calc(0.375em + 0.1875rem) center;
    background-size: calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    display: none;
}

.checkout-container .errorSelectField {
    border-color: #dc3545;
    padding-right: calc(1.5em + 0.75rem);
    background-image: url("data:image/svg+xml,%3csvg xmlns=%27http://www.w3.org/2000/svg%27 viewBox=%270 0 12 12%27 width=%2712%27 height=%2712%27 fill=%27none%27 stroke=%27%23dc3545%27%3e%3ccircle cx=%276%27 cy=%276%27 r=%274.5%27/%3e%3cpath stroke-linejoin=%27round%27 d=%27M5.8 3.6h.4L6 6.5z%27/%3e%3ccircle cx=%276%27 cy=%278.2%27 r=%27.6%27 fill=%27%23dc3545%27 stroke=%27none%27/%3e%3c/svg%3e");
    background-repeat: no-repeat;
    background-position: right calc(0.375em + 0.1875rem) center;
    background-size: calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);
}

.checkout-container .heading {
    font-weight: 600;
    line-height: 3.281rem;
    color: black;
    text-transform: uppercase;
    font-size: 2rem;
    align-self: center;
    letter-spacing: .028rem;
}

.checkout-container textarea {
    border-color: #a5a2a2;
    color: #a5a2a2;
    padding: 10px;
    font-size: 18px;
}

.checkout-container .order-section {
    padding: 0.1rem 2rem;
}

.checkout-container .order-section .heading {
    display: flex;
    justify-content: space-between;
    font-weight: 500;
    font-size: 20px;
    color: #818181;
    border-bottom: 4px solid;
}

.checkout-container .order-section .orders {
    display: flex;
    justify-content: space-between;
    margin: 6px 10px;
    color: #777777;
    border-bottom: 1px solid;
    padding-bottom: 10px;
}

.checkout-container .order-section .orders-total {
    display: flex;
    justify-content: space-between;
    margin: 6px 10px;
    color: #777777;
    border-bottom: 3px solid;
    padding-bottom: 10px;
    font-size: 1.2rem;
}

.checkout-container .order-section .payment {
    margin: 20px 10px;
}

.checkout-container .order-section .total-price {
    display: flex;
    line-height: 1.3rem;
    flex-direction: column;
}

.checkout-container .order-section .tax-info {
    font-size: 1rem;
}

@media screen and (min-width:768px) {
    .checkout-container textarea {
        /* width: 35%; */
    }
}

@media screen and (max-width:576px) {
    .text-style {
        font-size: 1rem;
    }

    .checkout-container .order-section .tax-info {
        font-size: 0.6rem;
    }
}